import {uploadimage} from '../../common/list.js'
export default{
	components:{
	},
	data() {
		return {
			relend:false,
			reldata:'正在提交...请勿关闭该页面',
			// 整个页面
			pageauth:true,
			// 判断公用上传参数
			top:'top',
			logo:'logo',
			flagg:false,
			isActive:false,
			enterprise:'',
			topimg:[],
			logoimg:[],
			account:'',
			bank:'',
			name:'',
			telephone:'',
			idcard:''
		}
	},
	
	methods:{
		//上传Logo和营业执照图片
		uploadImg(attribute){
			let quantity = 1
			uploadimage(quantity)
			.then((res) => {
				if(attribute === 'logo'){
					this.logoimg = res
				}else if(attribute === 'top'){
					this.topimg = res
				}
			})
		}
	}
}
